import { AuthService } from './../services/auth.service';
import { UserService } from './../services/user.service';
import { Component, OnInit } from '@angular/core';
import { LoginClient } from '../models/LoginClient';
import { Router } from '@angular/router';
import { User } from '../models/User';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private userService: UserService,
    private router: Router,
    private auth: AuthService
  ) { }
  user: User;
  login(user: LoginClient) {
    let data = this.userService.login(user).subscribe(data => {
      console.log("FROM LOGIN COMPONENT USER AUTH DATA: ", data)
      this.user = data;
    }, error => console.log(error), () => {
      this.auth.loginEvent(this.user);
      this.redirectToSuccesfulLogin(this.user.id)
    })
    //user$.subscribe(user => {
    // this.user = user;
    // this.userService.storeUser(user.id);
    //}, null, () => {
    // console.log(this.user)
    // })

  }
  redirectToSuccesfulLogin(id: number) {
    this.router.navigate(['/login/success/', id]);
  }
  ngOnInit() {
  }

}
