import { Comment } from './../../models/Comment';
import { CommentService } from './../../services/comment.service';
import { Article } from './../../models/Article';
import { ActivatedRoute } from '@angular/router';
import { ArticleService } from './../../services/article.service';
import { Component, OnInit, Input } from '@angular/core';


@Component({
  selector: 'app-forum-article',
  templateUrl: './forum-article.component.html',
  styleUrls: ['./forum-article.component.css']
})
export class ForumArticleComponent implements OnInit {
  id: number;
  article: Article;
  articleBody: Comment;
  @Input('newComment') newComment: Comment;

  constructor(
    private articleService: ArticleService,
    private commentService: CommentService,
    private route: ActivatedRoute) { }


  newCommentHandler(comment) {
    console.log("from forum article", comment)
    this.newComment = comment;
  }
  ngOnInit() {
    this.id = +this.route.snapshot.paramMap.get('id');
    this.articleService.get(this.id).subscribe(r => {
      this.article = r;
      this.articleBody = this.article.comments[0];
    });
  }

}



