import { QueryInfo } from './../models/QueryInfo';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Forum } from '../models/Forum';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ForumService {
  private url: string = "http://localhost:8080/forums";

  constructor(private http: HttpClient) { }

  private setPageParams(info: QueryInfo) {
    let Params = new HttpParams();
    Params = Params.append('page', String(info.page));
    Params = Params.append('size', String(info.size));

    return Params;
  }

  get(id: number) {

  }

  getAll(queryInfo: QueryInfo): Observable<Forum[]> {
    let Params = this.setPageParams(queryInfo);

    return this.http.get<Forum[]>(this.url, { params: Params });
  }
  getCategories() {
    return this.http.get(this.url + "/categories");
  }

  getByCategory(category) {
    return this.http.get<Forum[]>(this.url + "/" + category);
  }
  create(forum: Forum) {

  }
  update(forum: Forum) {

  }
  delete(id: number) {

  }
}



