import { QueryInfo } from './../models/QueryInfo';
import { Forum } from './../models/Forum';
import { ForumService } from './../services/forum.service';
import { Component, OnInit, Input } from '@angular/core';
import { OrderBy } from '../models/OrderBy';

@Component({
  selector: 'forum-list',
  templateUrl: './forum-list.component.html',
  styleUrls: ['./forum-list.component.css']
})
export class ForumListComponent implements OnInit {

  constructor(private forumService: ForumService) {
    
   }
  @Input('categories') categories;
  @Input('category') category;
  forums: Forum[];
  queryInfo: QueryInfo;

   getAllForums(){
     this.forumService.getAll(this.queryInfo).subscribe(r => {
       this.forums = r;
     });
   }
 
   getForumByCategory(){
    this.forumService.getByCategory(this.category).subscribe(r=>{
    this.forums = r;
    });
   }


  ngOnInit() {
    this.queryInfo = new QueryInfo(0, 2, OrderBy.ASC);
    this.getForumByCategory();
  console.log(this.forums);
  }

}



