import { Comment } from './../../models/Comment';
import { CommentService } from './../../services/comment.service';
import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'comment-form',
  templateUrl: './comment-form.component.html',
  styleUrls: ['./comment-form.component.css']
})
export class CommentFormComponent implements OnInit {
  newComment: Comment;

  @Input('articleId') articleId: number;
  @Output('commentAdded') commentAdded: EventEmitter<Comment>;

  constructor(private commentService: CommentService) {
    this.commentAdded = new EventEmitter();
  }

  postComment(form) {
    let comment = form.value;
    console.log(form);
    comment.articleId = this.articleId;
    comment.first = false;
    comment.id = null
    let newComment = this.commentService
      .post(comment).subscribe(
        response => {
          console.log(response);
          this.newComment = response;
        },
        error => console.log(error),
        () => {
          this.commentAdded.emit(this.newComment);
        });
  }
  ngOnInit() {
  }

}



