import { AuthService } from './services/auth.service';
import { StorageService } from './services/storage.service';
import { UserService } from './services/user.service';
import { TitleCasePipe } from './pipes/title-case.pipe';
import { ArticleService } from './services/article.service';
import { ForumComponent } from './forum/forum.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { MatInputModule } from '@angular/material/input';

import { AppComponent } from './app.component';
import { LocationStrategy, HashLocationStrategy } from '@angular/common';
import { BsNavbarComponent } from './bs-navbar/bs-navbar.component';
import { HomeComponent } from './home/home.component';
import { ForumsPortalComponent } from './forums-portal/forums-portal.component';
import { ForumService } from './services/forum.service';
import { ForumListComponent } from './forum-list/forum-list.component';
import { ForumCategoriesComponent } from './forum-categories/forum-categories.component';
import { ZippyComponent } from './zippy/zippy.component';
import { ForumArticleComponent } from './Article_Page/forum-article/forum-article.component';
import { CommentListComponent } from './Article_Page/comment-list/comment-list.component';
import { CommentService } from './services/comment.service';
import { LoginComponent } from './login/login.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { NgForm, FormsModule } from '@angular/forms';
import { CommentFormComponent } from './Article_Page/comment-form/comment-form.component';
import { LoginRedirectComponent } from './login-redirect/login-redirect.component';
import { BlogPortalComponent } from './Blog_Section/blog-portal/blog-portal.component';

const routes = [
  { path: "about", component: AboutUsComponent },
  { path: "blogs", component: BlogPortalComponent },
  { path: "login/success/:id", component: LoginRedirectComponent },
  { path: "login", component: LoginComponent },
  { path: "forums/:id", component: ForumComponent },
  { path: "article/:id", component: ForumArticleComponent },
  { path: "forums", component: ForumsPortalComponent },
  { path: "", component: HomeComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    BsNavbarComponent,
    HomeComponent,
    AboutUsComponent,
    ForumComponent,
    ForumsPortalComponent,
    ForumListComponent,
    TitleCasePipe,
    ForumCategoriesComponent,
    ZippyComponent,
    ForumArticleComponent,
    CommentListComponent,
    LoginComponent,
    CommentFormComponent,
    LoginRedirectComponent,
    BlogPortalComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    MatInputModule,
    NgbModule,
    NgbModule.forRoot(),
    RouterModule.forRoot(routes)
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    ArticleService, ForumService,
    CommentService, UserService,
    StorageService, AuthService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



