import { Comment } from './../../models/Comment';
import { CommentService } from './../../services/comment.service';
import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';


@Component({
  selector: 'comment-list',
  templateUrl: './comment-list.component.html',
  styleUrls: ['./comment-list.component.css']
})
export class CommentListComponent implements OnInit, OnChanges {

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.newComment.currentValue) {
      if (!changes.newComment.previousValue) {
        this.comments.push(changes.newComment.currentValue);
      } else
        if (changes.newComment.currentValue.id != changes.newComment.previousValue.id) {
          this.comments.push(changes.newComment.currentValue);
        }
    }
  }

  @Input("id") id: number;
  @Input('newComment') newComment: Comment;
  comments: any;
  constructor(private commentService: CommentService) { }

  getComments() {
    this.commentService.getByArticle(this.id).subscribe(comments => {
      this.comments = comments;
    })
  }


  ngOnInit() {
    this.getComments();
  }

}



