import { ClientAuthData } from './../models/ClientAuthData';
import { AuthService } from './auth.service';
import { HttpClient } from '@angular/common/http';
import { Injectable, EventEmitter } from '@angular/core';
import { LoginClient } from '../models/LoginClient';
import { Observable } from 'rxjs/Observable';
import { User } from '../models/User';
import { StorageService } from './storage.service';
import 'rxjs/add/operator/map';


@Injectable()
export class UserService {
  private url: string = "http://localhost:8080/users";
  userId: number;
  authData: ClientAuthData;
  constructor(
    private http: HttpClient,
    private storage: StorageService,
    private auth: AuthService) {
  }

  login(user: LoginClient): Observable<User> {
    return this.http.get<User>(`${this.url}/login/${user.email}/${user.password}`)

  }

  getUserById(id: number) {
    return this.http.get<User>(`${this.url}/${id}`);
  }
  storeUser(id: number) {
    let user = this.getUserById(id);
    console.log(user);
    user.subscribe(u => console.log(u))
    //this.userId = id;
    //this.storage.storeUser(id);
  }
}



