import { AuthService } from './services/auth.service';
import { Component } from '@angular/core';
import { User } from './models/User';
import { StorageService } from './services/storage.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  //might wanna remove..
  user: User;


  constructor(private authService: AuthService, private storage: StorageService) {
    authService.authEmitter.subscribe(event => {
      this.user = event;
      storage.storeUser(event);
    })
    this.user = storage.getUser();
  }

  logout() {
    console.log("logout event reached appcomponent")
    this.storage.deleteUser();
    this.user = null;
  }
}



